import React from "react";

interface TodoFormProps {
  addHandler(title: string): void
}

export const TodoForm: React.FC<TodoFormProps> = ({ addHandler }) => {
  const [title, setTitle] = React.useState<string>("");

  const changeHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    setTitle(event.target.value);
  }
  
  const onKeyPressHandler = (event: React.KeyboardEvent) => {
    if (event.key === "Enter") {
      addHandler(title);
      setTitle("");
    }
  }

  const onClickHandler = (event: React.MouseEvent<HTMLElement>) => {
    addHandler(title);
    setTitle("");
  }

  return (
    <div className="input-field col s6 row">
      <div className="col s9 xl11 m10">
        <input 
          onChange={changeHandler}
          onKeyPress={onKeyPressHandler}
          value={title}
          placeholder="Enter Todo Title"
          type="text" 
        />
      </div>
      <div className="col s3 xl1 m2">
        <button 
          onClick={onClickHandler}
          type="button" 
          className="btn light-blue accent-4"
        >
          Add
        </button>
      </div>
    </div>
  )
}